<template>
	<div id="mall-order-comment">
		<public-header title="添加评论"></public-header>
		<div class="content">
			<template v-for="(record, recordIndex) in records">
				<template v-if="record.order.order_type == 1 && record.order.parent_oid == 0">
					<div class="delivery-service">
						<div class="content-block-title">
							<i class="icon icon-waimai font-18"></i>
							<span>配送评价</span>
						</div>
						<van-cell-group>
							<van-cell>
								<div class="flex" slot="title">
									<span class="van-cell-text">配送服务</span>
									<van-rate class="flex" v-model="record.delivery_service" :size="20" :count="5" color="#ff2d4b" void-color="#FF6C81"/>
								</div>
							</van-cell>
						</van-cell-group>
					</div>
				</template>
				<div class="record-item">
					<div class="content-block-title van-hairline--bottom">
						<i class="icon icon-shop font-16"></i>
						<span>{{record.store.title}}</span>
					</div>
					<van-cell-group class="border-0px">
						<van-cell>
							<div class="flex" slot="title">
								<span class="van-cell-text">商品质量</span>
								<van-rate class="flex" v-model="record.goods_quality" :size="20" :count="5" color="#ff2d4b" void-color="#FF6C81"/>
							</div>
						</van-cell>
						<van-cell :title="item.goods_title" v-for="(item, index) in record.goods" :key="index">
							<div slot="right-icon" class="favor-oppose">
								<label @click="favorOppose(index, recordIndex, 1)">
									<input type="radio" class="radio" value="1" :checked="item.activity == 1">
									<span class="favor"></span>
								</label>
								<label @click="favorOppose(index, recordIndex, 2)">
									<input type="radio" class="radio" value="2" :checked="item.activity == 2">
									<span class="oppose"></span>
								</label>
							</div>
						</van-cell>
					</van-cell-group>
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<div class="content-block-title van-hairline--bottom">写点什么</div>
					<van-cell-group class="border-0px">
						<van-field v-model="record.note" type="textarea" placeholder="至少输入10个字，您的建议很重要，来点评一下吧" />
					</van-cell-group>
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<div class="content-block-title van-hairline--bottom">有图有真相</div>
					<uploader @onGetUrl="onGetImgsUrl" :value="record.thumbs" :max="6" :multiple="1" :uploaderIndex="recordIndex"></uploader>
				</div>
			</template>
			<div class="save-btn">
				<van-button size="large" @click="onSubmit()">提交评论</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Uploader from '@/components/uploader'
	export default {
		components: {
			PublicHeader,
			Uploader
		},
		data() {
			return {
				id: 0,
				records: [],
				islegal: false,
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				if(!this.id) {
					this.util.$toast('订单不存在或已删除！', this.util.getUrl({path: '/mall/pages/order/index', query: {}}), 1500, 'replace');
					return false;
				}
				this.util.request({
					url: 'wmall/mall/comment/index',
					data: {
						id: this.id
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.islegal = true;
					result = result.message;
					this.records = [...result.records];
				});
				if(this.util.isGlala()) {
					this.util.requestPermissionList({
						perm: ['storage', 'camera', 'photos']
					});
				}
			},

			favorOppose(goodsIndex, recordIndex, type) {
				this.records[recordIndex].goods[goodsIndex].activity = type;
			},
			onGetImgsUrl(data) {
				this.records[data.index].thumbs = [...data.value];
			},
			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				var records = this.records;
				for(var i in records) {
					if(records[i].order.order_type == 1 && records[i].order.parent_oid == 0 && !records[i].delivery_service) {
						this.util.$toast('请您评价配送服务');
						return false;
					}
					if(!records[i].goods_quality) {
						this.util.$toast('请您评价商品质量');
						return false;
					}
				}
				var params = {
					id: this.id,
					records: JSON.stringify(records)
				}
				this.util.request({
					url: 'wmall/mall/comment/post',
					method: 'post',
					data: params
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message)
						return false;
					}
					this.util.$toast(result.message, this.util.getUrl({path: '/mall/pages/order/detail', query: {id: this.id}}), 1500 , 'replace');
				})

			}
		},
		mounted() {
			var query = this.util.parseQuery(this.$route.query);
			this.id = query.id;
			this.onLoad()
		}
	}
</script>

<style>
	#mall-order-comment .record-item, #mall-order-comment .delivery-service{
		margin: 10px;
		border-radius: 5px;
		overflow: hidden;
		background-color: #fff;
	}
	#mall-order-comment .content-block-title{
		position: relative;
		overflow: hidden;
		font-size: 14px;
		line-height: 1;
		color: #6d6d72;
		text-overflow: ellipsis;
		text-transform: uppercase;
		white-space: nowrap;
		padding: 15px;
	}
	#mall-order-comment .van-cell-group .van-cell-text{
		padding-right: 10px;
		font-size: 15px;
	}
	#mall-order-comment .favor-oppose span {
		display: inline-block;
		width: 45px;
		height: 28px;
		margin-bottom: -7px;
	}
	#mall-order-comment .favor-oppose input[type=radio] {
		display: none;
	}
	#mall-order-comment .favor-oppose .favor {
		background: url(../../../../static/img/favor_grey.png) no-repeat;
		background-size: cover;
	}
	#mall-order-comment .favor-oppose input[type=radio]:checked + .favor {
		background: url(../../../../static/img/favor.png) no-repeat;
		background-size: cover;
	}
	#mall-order-comment .favor-oppose .oppose {
		background: url(../../../../static/img/oppose_grey.png) no-repeat;
		background-size: cover;
	}
	#mall-order-comment .favor-oppose input[type=radio]:checked + .oppose {
		background: url(../../../../static/img/oppose.png) no-repeat;
		background-size: cover;
	}
	#mall-order-comment .img-container{
		background-color: #fff;
		padding: 10px;
		padding-bottom: 0;
	}
	#mall-order-comment .img-container .van-col{
		position: relative;
		height: 60px;
		margin-bottom: 10px;
	}
	#mall-order-comment .img-container .van-col img {
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
	#mall-order-comment .img-container .van-col .icon{
		position: absolute;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		background: rgba(0,0,0,.6);
		right: -5px;
		top: -10px;
		z-index: 10000;
		color: #fff;
		text-align: center;
		font-size: 14px;
		line-height: 20px;
	}
	#mall-order-comment .img-container .van-col .van-uploader{
		width: 100%;
		height: 100%;
	}
	#mall-order-comment .save-btn{
		margin: 10px;
	}
	#mall-order-comment .save-btn .van-button{
		background-color: #ff2d4b;
		color: #fff;
		border-radius: 5px;
	}
</style>



